<template>
<div class="board" >
                <div class="last-move" v-if="!winner">Last move : {{lastMove}}</div>
                    <div v-else class="last-move"> 
            {{winner}}
            </div>


    <div class="line" v-for="i in 3" :key="i" :class="{disabled:gameFinished}">
        <div @click="updateSquare(i-1,j-1)" 
        class="squares" :class="{colored: (row.includes(i-1) && column.includes(j-1)) || (diagonal1 && i==j) || (diagonal2 &&
        i-1+j-1==2)}"
            v-for="j in 3" :key="j">{{board[i-1][j-1]}}</div>
    </div>

    <button @click="restart" v-if="winner">Play again</button>
</div>

               

</template>

<script>
import { ref } from '@vue/reactivity'
export default {
    
    setup(){
        let board = ref([
            ['','',''],
            ['','',''],
            ['','','']
        ])
        const lastMove = ref('-')
        const winner = ref(null)
        const gameFinished = ref(false)
        let full = null
        const row = ref([])
        const column = ref([null])
        const diagonal1 = ref(false)
        const diagonal2 = ref(false)

        const updateSquare = (i,j)=>{


            if(lastMove.value == '-'){
                lastMove.value = 'X'
                board.value[i][j] = lastMove.value
            }
            if(!board.value[i][j]){
                    lastMove.value = lastMove.value == 'X' ? 'O': 'X'
                    board.value[i][j] = lastMove.value
            }

             winner.value = checkWinner()
            full = isFull()
            if(!winner.value && full) {
              gameFinished.value = true
              winner.value = 'This is a Draw'
            }
            else if(winner.value || full){
                gameFinished.value = true
            }
        }

        const checkWinner = ()=>{

            //check vertical
            for(let j = 0; j < 3 ; j++){
            if( board.value[0][j] == board.value[1][j] &&  board.value[1][j] == board.value[2][j] && board.value[2][j] != '' )
            {        column.value[0] = j
                     row.value.push(0,1,2)
                    return 'The winner is '+lastMove.value

            }

            }

            //check horizontal
            for(let i = 0; i < 3 ; i++){
            if( board.value[i][0] == board.value[i][1] &&  board.value[i][1] == board.value[i][2] && board.value[i][2] != '')
            {        row.value[0] = i
                    column.value.push(0,1,2)
                    return 'The winner is '+lastMove.value

            }
            }


            //check diagonal
            if ( board.value[0][0] == board.value[1][1] &&  board.value[1][1] == board.value[2][2] &&  board.value[2][2] !='') {
                        diagonal1.value= true
                        return 'The winner is '+lastMove.value

            }
            else if( board.value[0][2] == board.value[1][1] &&  board.value[1][1] == board.value[2][0] &&  board.value[2][0] !=''){
                        diagonal2.value= true
                        return 'The winner is '+lastMove.value

            }


                return null
        }

        const isFull = ()=>{

            for(let i = 0 ; i < 3 ; i++){
                for (let j = 0 ; j < 3 ; j++){
                    if( board.value[i][j] == '') return false
                }
            }
            return true
        }

        const restart = ()=>{
                board.value = [
            ['','',''],
            ['','',''],
            ['','','']
        ],
            winner.value = null
            lastMove.value = '-'
            gameFinished.value = false
            full = null
            row.value = []
            column.value = []
            diagonal1.value = false
            diagonal2.value = false


        }
  

        return {board , lastMove , updateSquare , winner , gameFinished , restart , row , column ,diagonal1,diagonal2}
    }
}
</script>

<style>
.board{
    text-align: center;
    margin-top : 2em;


}
.squares{
    border : 3px solid rgb(255, 191, 72);
    width : 140px;
    height : 140px;
    text-align: center;
    font-size : 6em;
    line-height: 140px;
    cursor: pointer;
    background: rgb(255, 255, 255);
    box-shadow: 2px 3px 15px rgb(255 191 72 / 68%);
    color: rgb(255, 191, 72);

}
.squares:active{
    border-color: #222;
}
.line{
    display: flex;
    justify-content: center;
}
.disabled{
    pointer-events: none;
}
.last-move{
    background :rgb(255, 191, 72);
    padding: 1em;
    margin-bottom : 1em;
}
button{
    outline : none;
    border : none;
    padding : 1em 2em;
    margin : 2em 0;
    background : rgb(255, 191, 72);
    border-radius : 4px;
    cursor: pointer;
}
button:hover{
    opacity : .8;
}
.colored{
    background : rgb(255, 191, 72);
    color : white;
    border-color : black;
}
</style>